import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { registrationAPI } from "../../API/api";

const initialState = {
  usersData: [],
};

export const userSliceReducer = createSlice({
  name: "userSlice",
  initialState,
  reducers: {
    getUserData(state, action) {
      state.usersData = action.payload;
    },
  },
});

export const postUserData = createAsyncThunk(
  "userSlice/postUserData",
  async (formData, { dispatch }) => {
    try {
      await registrationAPI.postUserData(formData);
    } catch (error) {
      console.error("ERROR", error);
    }
  }
);

function remix(dispatch, actionCreator, data) {
  console.log("remix", data);
  return dispatch(actionCreator(data));
}

export const getUsersData = createAsyncThunk(
  "userSlive/getUsersData",
  async (_, { dispatch }) => {
    try {
      const data = await registrationAPI.getUsersData();
      // dispatch(getUserData(data));
      remix(dispatch, getUserData, data);
    } catch (error) {
      console.error("ERROR", error);
    }
  }
);

export const putUserData = createAsyncThunk(
  "userSlice/putUserData",
  async ({ id, formData }, { dispatch }) => {
    try {
      await registrationAPI.putUserData(id, formData);
      dispatch(getUsersData());
    } catch (error) {
      console.error("ERROR", error);
    }
  }
);

export const postUserAvatar = createAsyncThunk(
  "userSlice/postUserAvatar",
  async (formData, { dispatch }) => {
    try {
      const avatarUrl = await registrationAPI.postUserAvatar(formData);
      console.log(avatarUrl.data.display_url);
      return avatarUrl.data.display_url;
    } catch (error) {
      console.error("ERROR", error);
    }
  }
);

export const { getUserData } = userSliceReducer.actions;
export default userSliceReducer.reducer;
